import React, { FC, PropsWithChildren, ReactNode } from 'react'
import { ProviderWeb3, ProviderWeb3Props } from 'reef-knot/web3-react'

import { CookiesTooltip, CookieThemeProvider, ToastContainer } from '@lidofinance/lido-ui'
import { Header, Main, Footer, NavigationLinkProps } from '@lidofinance/next-widget-layout'
import { ProviderWalletModal, ProviderWalletModalProps } from '@lidofinance/eth-ui-wallet-modal'

import { AppWagmiConfig, AppWagmiConfigProps } from './providers'

export type WidgetAppProps = {
  navigation: ReactNode
  headerActions: ReactNode
  navigationLinks?: NavigationLinkProps[] | undefined
  disableCookiesTooltip?: boolean | undefined
  disableToasts?: boolean | undefined
}

export type WidgetAppEVMProps = WidgetAppProps & {
  wagmi: AppWagmiConfigProps
  providerWeb3: ProviderWeb3Props
  reefKnot: ProviderWalletModalProps
}

export const WidgetApp: FC<PropsWithChildren<WidgetAppProps>> = (props) => (
  <CookieThemeProvider>
    <Header headerActions={props.headerActions}>{props.navigation}</Header>
    <Main>{props.children}</Main>
    <Footer />
    {!!props.disableCookiesTooltip && <CookiesTooltip />}
    {!!props.disableToasts && <ToastContainer />}
  </CookieThemeProvider>
)

export const EVMWidgetApp: FC<PropsWithChildren<WidgetAppEVMProps>> = (props) => (
  <AppWagmiConfig {...props.wagmi}>
    <ProviderWeb3 {...props.providerWeb3}>
      <ProviderWalletModal {...props.reefKnot}>
        <WidgetApp
          navigation={props.navigation}
          headerActions={props.headerActions}
          navigationLinks={props?.navigationLinks}
          disableCookiesTooltip={props?.disableCookiesTooltip}
          disableToasts={props?.disableToasts}
        >
          {props.children}
        </WidgetApp>
      </ProviderWalletModal>
    </ProviderWeb3>
  </AppWagmiConfig>
)
